.App{
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 100vh;
  font-family: 'Ubuntu', sans-serif;
  color: black;
  background: #360033;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #360033, #0b8793,);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #360033, #0b8793); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

img{
  width: 25%;
  height: 25%;
}

h1{
  text-transform: uppercase;
}

button{
  border-color: black;
  border-style: solid;
  border-radius: 25px;
  font-weight: bold;
  text-transform: uppercase;
  padding: 0.5rem;
  margin: 1rem;
  -webkit-box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.75);
  -moz-box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.75);
  box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.75);
}

button:hover{
  color: white;
  background: gray;
  cursor: pointer;
}